<template>
  <div class="dashboard-editor-container">
    <el-row>
      <panel-group @handleSetLineChartData="handleSetLineChartData"/>
    </el-row>
    <el-row>
      <el-col :span="24" class="chart-wrapper">
        <ve-line :data="chartData"></ve-line>
      </el-col>
      
    </el-row>
      <el-row>
        <el-col :span="12" class="chart-wrapper">
          <ve-histogram :data="chartData" :theme-name="asdfasdf"></ve-histogram>
        </el-col>
        <el-col :offset="1" :span="11" class="chart-wrapper"> 
          <ve-pie :data="pieChartData"></ve-pie>        
        </el-col>
      </el-row>
    </div>
</template>
<script>
import PanelGroup from '@/components/dashboard/PanelGroup'
import VeHistogram from 'v-charts/lib/histogram.common'
import VePie from 'v-charts/lib/pie.common'
import VeLine from 'v-charts/lib/line.common'

const lineChartData = {
  newVisitis: {
    expectedData: [100, 120, 161, 134, 105, 160, 165],
    actualData: [120, 82, 91, 154, 162, 140, 145]
  },
  messages: {
    expectedData: [200, 192, 120, 144, 160, 130, 140],
    actualData: [180, 160, 151, 106, 145, 150, 130]
  },
  purchases: {
    expectedData: [80, 100, 121, 104, 105, 90, 100],
    actualData: [120, 90, 100, 138, 142, 130, 130]
  },
  shoppings: {
    expectedData: [130, 140, 141, 142, 145, 150, 160],
    actualData: [120, 82, 91, 154, 162, 140, 130]
  }
}
export default {
  name:'StuStatistic',
   components: {
   PanelGroup,
   VeHistogram,
   VePie,
   VeLine
  },
  data(){
    return {
      asdfasdf:'asdfasdf',
      chartData: {
          columns: ['', '', '', ''],
          rows: [
            { '': '', '': 1393, '': 1093, '': 0.78 },
            { '': '', '': 3530, '': 3530, '': 0.78 },
            { '': '', '': 2923, '': 2623, '': 0.76 },
            { '': '', '': 1723, '': 1423, '': 0.49 },
            { '': '', '': 3792, '': 3792, '': 0.323 },
            { '': '', '': 4593, '': 4293, '': 0.78 }
          ]
       },
       pieChartData: {
          columns: ['', ''],
          rows: [
            { '': '', '': 1393 },
            { '': '', '': 3530 },
            { '': '', '': 2923 },
            { '': '', '': 1723 },
            { '': '', '': 3792 },
            { '': '', '': 4593 }
          ]
        }
    }
  },
  methods:{
        handleSetLineChartData(type) {
      this.lineChartData = lineChartData[type]
    },
    
  }
  
}
</script>
<style lang="scss">
.dashboard-editor-container {
  padding: 32px;
  background-color: rgb(240, 242, 245);
  .chart-wrapper {
    background: #fff;
    padding: 16px 16px 0;
    margin-bottom: 32px;
  }
}
</style>





